
*, *::after, *::before {
    box-sizing: border-box;
}

header {
    position: absolute;
    top: 60px;
    left: 60px;
    right: 60px;
}
header .header-inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
header .header-inner .header-inner-col .header-inner-logo h2 {
    line-height: 1;
    margin: 0;
    font-size: 1.5rem;
}
header .header-inner .header-inner-col .header-inner-nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
header .header-inner .header-inner-col .header-inner-nav .header-inner-nav-link {
    display: block;
    font-size: 1.2rem;
}
header .header-inner .header-inner-col .header-inner-nav .header-inner-nav-link:first-child {
    margin-right: 60px;
}
header .header-inner .header-inner-col .header-inner-nav .header-inner-nav-menu {
    display: flex;
    align-items: center;
}
header .header-inner .header-inner-col .header-inner-nav .header-inner-nav-menu .header-inner-nav-link {
    margin-right: 20px;
}
header .header-inner .header-inner-col .header-inner-nav .header-inner-nav-menu .header-inner-nav-menu-hamburger {
    width: 24px;
    position: relative;
    top: 1px;
}
header .header-inner .header-inner-col .header-inner-nav .header-inner-nav-menu .header-inner-nav-menu-hamburger span {
    border: 1px solid black;
    position: relative;
    display: block;
    width: 100%;
    height: 2px;
    margin: 5px 0;
}
.cursor {
    position: fixed;
    top: 0px;
    left: 0px;
    z-index: 10;
    contain: layout style size;
    pointer-events: none;
}
.cursor.media-blend {
    z-index: 100;
    mix-blend-mode: exclusion;
}
.cursor.media-blend .cursor-media {
    filter: invert(1);
}
.cursor:before {
    content: "";
    position: absolute;
    top: -24px;
    left: -24px;
    display: block;
    width: 48px;
    height: 48px;
    transform: scale(0.2);
    background: black;
    border-radius: 50%;
}
.cursor .cursor-media {
    display: block;
    height: 350px;
    width: 350px;
    overflow: hidden;
    border-radius: 100%;
    transform: scale(0);
    margin: -175px 0 0 -175px;
    position: relative;
}
.cursor .cursor-media video {
    height: 350px;
    left: 50%;
    transform: translateX(-50%);
    position: absolute;
}
